<template>
  <div class="classManagement mt72">
    <div class="myClass">
      <!-- <i class="my-i">|</i>
      <span>我的教研组</span>-->
    </div>
    <el-row :gutter="24">
      <el-col :span="9" style="padding-left:50px">
        <div class="grid-content bg-purple cp" @click="createClass">
          <p class="add">+</p>
          <p class="newClass">创建新教研组</p>
        </div>
        <div class="researchCode">
          <el-input class="resCode" placeholder="请输入邀请码"></el-input>
          <el-button size="small" class="addRes">加入</el-button>
        </div>
      </el-col>

      <el-col
        :span="15"
        v-for="(item, i) in classList"
        :key="i"
        style="border-left:1px solid #eee;padding-left: 0px;"
      >
        <div class="nowBox">
          <div class="title">
            <p class="nowClass">当前参加的教研组</p>
          </div>
          <div class="myBox">
            <div class="litleBox" @click="research(item.id)">
              <p class="xueke">
                <span>八年级：初二物理</span>
              </p>
              <p class="myTearch">
                创建者：
                <span>物理学科、陈老师</span>
              </p>
              <div class="myTearchBox">
                <div class="myTearchLeft">学科教师：</div>
                <div class="myTearchRight">
                  <span>物理学科、陈老师</span>
                  <span>化学学科、陈老师</span>
                  <span>数学学科、陈老师</span>
                </div>
              </div>
              <p class="yqCode">邀请码:4324571</p>
              <p>创建时间:2019-11-11</p>
            </div>
          </div>
        </div>
        <div class="nowBox">
          <div class="title" style="background-color:#fec31c">
            <p class="nowClass">以往参加的教研组</p>
          </div>
          <div class="myBox" v-for="(item, index) in boxList" :key="index">
            <div
              class="litleBox"
              style="border: 1px solid #faf0c8;box-shadow: 10px 15px 0px 0px #faf0c8, 15px 10px 0px 0px #faf0c8;"
            >
              <p class="xueke" style=" border-bottom: 1px solid #faf0c8;">
                <span>八年级：初二物理</span>
              </p>
              <p class="myTearch">
                创建者：
                <span>物理学科、陈老师</span>
              </p>
              <div class="myTearchBox" style=" border-bottom: 1px solid #f9e7de;">
                <div class="myTearchLeft">学科教师：</div>
                <div class="myTearchRight">
                  <span>物理学科、陈老师</span>
                  <span>化学学科、陈老师</span>
                  <span>数学学科、陈老师</span>
                </div>
              </div>
              <p class="yqCode">邀请码:4324571</p>
              <p>创建时间:2019-11-11</p>
            </div>
          </div>
        </div>
      </el-col>
    </el-row>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: "classManagement",
  data() {
    return {
      classList: [],
      boxList: [1, 1, 1, 1],
      classIcon: sessionStorage.getItem("icon")
    };
  },
  created() {
    //获取创建的班级列表
    this.queryCreateClass();
    if (sessionStorage.getItem("wxicon")) {
      this.classIcon = sessionStorage.getItem("wxicon");
    } else if (sessionStorage.getItem("icon")) {
      this.classIcon = sessionStorage.getItem("icon");
    }
  },
  methods: {
    //获取创建的班级列表
    queryCreateClass() {
      this.$post("/likework/queryCreateClass.do").then(response => {
        //console.log(response)
        if (response.code == "5000") {
          this.classList = response.data;
        }
      });
    },
    createClass() {
      this.$router.push({
        path: "/homeCommon/classManagement/createClass"
      });
    },
    research(id) {
      //  console.log(2222);

      this.$router.push({
        path: "/homeCommon/TeachingResearch/ResearchList"
      });
    },
    deleteClass(item, i) {
      //  console.log(111)
      let data = {
        classId: item.id
      };
      this.$confirm("是否删除此班级", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$post("/likework/deleteClass.do", data).then(res => {
          if (res.code == "5000") {
            this.$message.success("删除成功!");
            this.classList.splice(i, 1);
          }
        });
      });
    }
  }
};
</script>

<style scoped lang="less">
.classManagement {
  // margin-top: 40px;
  background-color: #fff;
  .myClass {
    height: 40px;
    background-color: #f8f8f8;
    .my-i {
      color: #f25c29;
      font-size: 22px;
      font-weight: bold;
      line-height: 67px;
    }
    span {
      line-height: 67px;
      font-size: 20px;
    }
  }
  .content {
    // background: url("../../../../static/images/class_bgimg.jpg") no-repeat;
    // background: url("../../../../static/images/class_bgimg.jpg") ;
    background-color: #fff;
    height: 180px;
    text-align: center;
    padding-top: 28px;
    width: 100%;
    max-width: 270px;
    margin-top: 40px;
    position: relative;
    border-radius: 20px;
    .iconfont {
      position: absolute;
      width: 25px;
      height: 25px;
      right: 10px;
      top: 20px;
      font-size: 22px;
      display: none;
    }
    .img-icon {
      width: 65px;
      height: 65px;
      position: absolute;
      left: 10px;
      top: 80px;
      border-radius: 50%;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .iconfont:hover {
      color: #2592e7;
    }
    .name {
      font-size: 20px;
      position: relative;
      margin: 14px auto 0 auto;
      height: 34px;
    }
    .invitationCode {
      color: #b6b6b6;
      //   margin: 14px 0 0 0;
      margin-left: 25px;
    }
    .time {
      color: #b6b6b6;
      font-size: 12px;
      margin-left: 23px;
    }
  }
  .content:hover {
    .iconfont {
      display: block;
    }
  }
}

.nowBox {
  width: 100%;
  .title {
    height: 35px;
    background-color: #66c5ee;
    .nowClass {
      margin-left: 20px;
      line-height: 35px;
      font-size: 18px;
    }
  }
  .myBox {
    display: inline-block;
    width: 50%;
    padding: 30px;
    box-sizing: border-box;
    .litleBox {
      width: 269px;
      height: 174px;
      border: 1px solid #96e0f8;
      box-shadow: 10px 15px 0px 0px #96e0f8, 15px 10px 0px 0px #96e0f8;
      padding: 12px 20px;
      border-radius: 15px;
      .xueke {
        border-bottom: 1px solid #96e0f8;
        padding-bottom: 5px;
        font-size: 18px;
      }
      .myTearch {
        font-size: 16px;
        padding: 5px 0px;
      }
      .yqCode {
        padding-top: 5px;
      }
      .myTearchBox {
        width: 100%;
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid #96e0f8;
        padding-bottom: 5px;
        .myTearchLeft {
          width: 30%;
          font-size: 16px;
        }
        .myTearchRight {
          width: 70%;
          span {
            float: left;
          }
        }
      }
    }
  }
}
.researchCode {
  margin-top: 35px;
  width: 270px;
  height: 149px;
  border: 1px solid #eee;
  background-color: #fff;
  border-radius: 15px;
  box-shadow: 5px 5px 0px 0px #eee, 5px 5px 0px 0px #eee;
  .resCode {
    margin-top: 50px;
    width: 200px;
    margin-left: 38px;
  }
  .addRes {
    margin-left: 110px;
    margin-top: 10px;
    border: 0;
    background-color: #2ab8b3;
    color: #fff;
  }
}
.bg-purple {
  //  background: url("../../../../static/images/add_class2.jpg") no-repeat;
  // height: 151px;
  text-align: center;
  //  padding-top: 28px;
  height: 180px;
  width: 100%;
  max-width: 270px;
  margin-top: 40px;
  background-color: #fff;
  border-radius: 20px;
  border: 1px solid #eee;
  box-shadow: 5px 5px 0px 0px #eee, 5px 5px 0px 0px #eee;
  .add {
    font-size: 80px;
    color: #f25c29;
    font-weight: normal;
  }
  .newClass {
    color: #b6b6b6;
  }

  // background: url(../../../../static/images/add_class2.jpg);
}
.model_title {
  width: 100%;
  text-align: center;
  position: relative;
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  border-bottom: 1px solid #eee;
  .title {
    position: relative;
    display: inline-block;
    padding: 0 10px;
    box-sizing: border-box;
    .tits {
      float: left;
      position: absolute;
      left: -110px;
      top: -18px;
      color: black;
      // top: 50%;
      // left: 50%;
      // transform: translate(-50%;-50%);
    }
    .icon_l {
      position: absolute;
      width: 25px;
      height: 8px;
      right: -25px;
      top: 6px;
      background: url("../../../../static/images/icon_r.png") right center
        no-repeat;
      display: inline-block;
    }
    .icon_r {
      position: absolute;
      width: 25px;
      height: 8px;
      left: -25px;
      top: 6px;
      background: url("../../../../static/images/icon_l.png") left center
        no-repeat;
      display: inline-block;
    }
  }
}
.name {
  width: 100%;
  text-align: center;
  position: relative;
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  .names {
    position: relative;
    display: inline-block;
    padding: 0 10px;
    box-sizing: border-box;
    font-size: 16px;
  }
  .right-arrow {
    position: absolute;
    width: 9px;
    height: 17px;
    right: -9px;
    top: 6px;
    // background: url("../../../../static/images/arrow_r.png") right center
    //   no-repeat;
    display: inline-block;
  }
}
</style>



